/**
Name: Tangible Light
Based on IntelliJ IDEA default theme by JetBrains
*/
.cm-s-tangible-light,
.CodeMirror-hints.tangible-light {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
}

.cm-s-tangible-light span.cm-number {
  color: #0000FF;
}

.cm-s-tangible-light span.cm-string {
  color: #4dbb00;
}

.cm-s-tangible-light span.cm-string-2 {
  color: #4dbb00;
}

.cm-s-tangible-light span.cm-keyword {
  color: #000080;
}

.cm-s-tangible-light span.cm-atom {
  color: #0000FF;
}

.cm-s-tangible-light span.cm-def {
  color: #000000;
}

.cm-s-tangible-light span.cm-tag,
.cm-s-tangible-light span.cm-variable,
.cm-s-tangible-light span.cm-variable-3 {
  color: #000080;
}

/* .cm-s-tangible-light span.cm-variable-2 { color: black; }
.cm-s-tangible-light span.cm-variable-3, .cm-s-tangible-light span.cm-type { color: black; } */
.cm-s-tangible-light span.cm-operator {
  color: black;
}

.cm-s-tangible-light span.cm-comment {
  color: #808080;
}

.cm-s-tangible-light span.cm-qualifier {
  color: #555;
}

.cm-s-tangible-light span.cm-error {
  color: #FF0000;
}

.cm-s-tangible-light span.cm-dynamic-tag,
.cm-s-tangible-light span.cm-variable-2 {
  color: #a100b9;
}

.cm-s-tangible-light span.cm-link {
  color: #0000FF;
}

.cm-s-tangible-light .CodeMirror-activeline-background {
  background: #ebf3fd;
}

.cm-s-tangible-light span.cm-builtin {
  color: #30a;
}

.cm-s-tangible-light span.cm-property,
.cm-s-tangible-light span.cm-attribute,
.cm-s-tangible-light span.cm-meta {
  color: #0000FF;
}

.cm-s-tangible-light span.cm-bracket {
  color: #997;
}

.cm-s-tangible-light .CodeMirror-matchingbracket {
  outline: 1px solid #ccc;
  color: black !important;
}

.cm-s-tangible-light .CodeMirror-matchingtag {
  background-color: transparent;
  border-bottom: 1px dotted #30a;
}

.CodeMirror-hints.tangible-light {
  color: #616569;
  background-color: #ebf3fd !important;
}

.CodeMirror-hints.tangible-light .CodeMirror-hint-active {
  background-color: #a2b8c9 !important;
  color: #5c6065 !important;
}

